import React from "react"
import { useEffect } from "react"
import { useDispatch} from "react-redux"
import { setPage } from "../../BLL/Reducers/reasonReducer"
import { reasonsSelector, pageSelector, portionSizeElementsSelector, portionSizeSelector } from "../../BLL/Selectors/reasonSelector"
import { Template } from "../Bricks/Template"



type PropsType={
    isPC:boolean
}
export const Reason:React.FC<PropsType>=({isPC})=>{
    
    useEffect(()=>{
        return ()=>{
            dispatch(setPage(1))
        }
    },[])


    const dispatch=useDispatch()
    
    return<Template 
        isPC={isPC}
        title={"Причины побить"}
        setPage={setPage}
        pageSelector={pageSelector}
        portionSizeElementsSelector={portionSizeElementsSelector}
        portionSizeSelector={portionSizeSelector}
        elementsSelector={reasonsSelector}/>
}

